import Vue from 'vue';
import DashboardPlugin from './plugins/dashboard-plugin';
import App from './App.vue';

// router setup
import router from './routes/router';

import './registerServiceWorker'
import VueLazyload from 'vue-lazyload'
import store from './store'
import preloader from '@/assets/img/preloader.svg'

import bugsnag from '@bugsnag/js'
import bugsnagVue from '@bugsnag/plugin-vue'

const bugsnagClient = bugsnag('7d7e011d70ccb617f2dfcfdc8a546b3c');
bugsnagClient.use(bugsnagVue, Vue);


import * as atatus from 'atatus-spa';
atatus.config('35b08f4023d04fc38b6aec5460f60f7e').install();

import titleMixin from '@/mixins/titleMixin'

import VueAnalytics from 'vue-analytics'

import axios from 'axios'
import VueAxios from 'vue-axios'


Vue.use(VueAxios, axios);

// plugin setup
Vue.use(DashboardPlugin);

// Video Player
import VuePlyr from 'vue-plyr'

Vue.use(VuePlyr);

//Lazy load images
Vue.use(VueLazyload, {
  loading: preloader,
  error: preloader,
  attempt: 1
});

const isProd = process.env.NODE_ENV === 'production';

Vue.use(VueAnalytics, {
  id: 'UA-1176295-62',
  router,
  debug: {
    enabled: !isProd,
    sendHitTask: isProd,
  }
});

// Vue.filter('truncate', function (text, stop, clamp) {
//   return text.slice(0, stop) + (stop < text.length ? clamp || '...' : '')
// });

Vue.mixin(titleMixin);

var _oldOnError = Vue.config.errorHandler;
Vue.config.errorHandler = function VueErrorHandler(error, vm) {
  atatus.notify(error, {
    extra: {
      componentName: Vue.util.formatComponentName(vm),
      propsData: vm.$options.propsData
    }
  });

  if (typeof _oldOnError === 'function') {
    _oldOnError.call(this, error, vm);
  }
};

/* eslint-disable no-new */
new Vue({
  el: '#app',
  render: h => h(App),
  store,
  router
});
